@use '../start.scss';

@mixin alert() {
  background : rgb(253, 239, 239);
  padding : 15px;
  border-radius : 5px;
  max-width : 500px;
  width : 100%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 20px;
}

.my-alert {
  @include alert()
}

.my-alert p {
  margin-bottom : 0;
}

.wow-enter {
  opacity: 0;
}

.wow-enter-active {
  opacity: 1;
  transition: all 500ms;
}